import React, { useState } from "react";
import { getManifest } from "src/api/missionManifests";
import { getEmptyManifest } from "src/entities/Manifest";

const ManifestPage: React.FC = () => {
  const [rover, setRover] = useState("Curiosity");
  const [manifest, setManifest] = useState(getEmptyManifest());

  const handleSelectChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setRover(event.target.value);
  };
  const handleButton = () => {
    getManifest(rover).then((apiResp) => {
      setManifest(apiResp);
    });
  };
  return (
    <div className='center'>
      <div className='selector'>
        <select value={rover} onChange={handleSelectChange}>
          <option value='Curiosity'>Curiosity</option>
          <option value='Opportunity'>Opportunity</option>
          <option value='Spirit'>Spirit</option>
        </select>
        <br />
        <button type='button' onClick={handleButton}>
          Случайная картинка
        </button>
        <br />
      </div>
      <div className='manifest'>
        <label>
          Name of the Rover:
          <input type='text' value={manifest.name} disabled />
        </label>
        <label>
          The Rover&apos;s landing date on Mars:
          <input type='text' value={manifest.landing_date} disabled />
        </label>
        <label>
          The Rover&apos;s launch date from Earth:
          <input type='text' value={manifest.launch_date} disabled />
        </label>
        <label>
          The Rover&apos;s mission status:
          <input type='text' value={manifest.status} disabled />
        </label>
        <label>
          The most recent Martian sol from which photos exist:
          <input type='text' value={manifest.max_sol} disabled />
        </label>
        <label>
          The most recent Earth date from which photos exist:
          <input type='text' value={manifest.max_date} disabled />
        </label>
        <label>
          Number of photos taken by that Rover:
          <input type='text' value={manifest.photos.length} disabled />
        </label>
      </div>
    </div>
  );
};

export default ManifestPage;
